import { HandleProvider } from './../../../providers/handle/handle';
import { Device } from '@ionic-native/device';
import { StorageService } from './../../../services/storage-service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the HandleHistoryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-handle-history',
  templateUrl: 'handle-history.html',
})
export class HandleHistoryPage {

  type: number;
  types: any;
  history: any[];
  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    public storageService: StorageService,
    public device: Device,
    public handleProvider: HandleProvider) {
      this.types = [{
        name: 'all', value: 999
      },{
        name: 'error', value: -1
      },{
        name: 'sqlite', value: 1
      },{
        name: 'normal', value: 0
      }] 
      this.type = this.types[0].value 
      this.history = this.handleProvider.get(this.type);
       
  }
 
  doRefresh(events){
    this.history = this.handleProvider.get(this.type);
    setTimeout(()=>{
      events && events.complete();
    },500)
  }

  clear(){
    this.handleProvider.clear();
    this.history = this.handleProvider.get(this.type);
  }

  openHandleDetailsPage(item){
    this.navCtrl.push('HandleDetailsPage',item)
  }

   
  
}



